import React from "react"

export default function Die(props) {
    const styles = {
        backgroundColor: props.isheld ? "#59E391" : "white"
    }

    const nums = {
        1: "one",
        2: "two",
        3: "three",
        4: "four",
        5: "five",
        6: "six"
    }
    
    function getDots(n){
        let dots = [];
        for(let i = 1; i <= n; i++){
            dots.push(<div className={nums[i]}></div>);
        }
        return dots;
    }

    return (
        <div className="die-face" style={styles} onClick={props.test}>
            <div className="die-num">
                {getDots(props.value)}
            </div>
        </div>
    )
}