import React from 'react';
import './App.css';
import Die from './Die';
import useWindowSize from 'react-use-window-size'
import Confetti from 'react-confetti'
import aud from "./applause.wav";
import Voice_gif from './voice.gif';
function App() {
  const [nums, setNums] = React.useState(allNewDie());

  const [tenzies, setTenzies] = React.useState(false);

  const [count, setCount] = React.useState(0);
  
  React.useEffect(() => {
    const allHeld = nums.every(die => die.isHeld)

    const firstValue = nums[0].randNum;
    const allSame = nums.every(die => die.randNum === firstValue)
    if (allHeld && allSame) {
      console.log("Won");
      setTenzies(true);
    }
  }
    , [nums]);


  function generateDie() {
    return (
      {
        randNum: Math.ceil(Math.random() * 6),
        isHeld: false,
        id: Math.random()
      })
  }


  function allNewDie() {
    let nums = [];
    for (let i = 0; i < 10; i++) {
      nums.push(generateDie())
    }
    return nums;
  }

  function testing(id) {
    setNums(oldNums => oldNums.map(die => die.id === id ? { ...die, isHeld: !die.isHeld } : die))
  }

  function changeNums() {
    if (!tenzies) {
      setNums(oldDie => oldDie.map(die => die.isHeld ? die : generateDie()));
      setCount(count => count + 1);
    }
    else {
      setTenzies(false);
      setNums(allNewDie());
      setCount(0);
    }
  }

  const diceElements = nums.map(
    die => <Die
      value={die.randNum}
      isheld={die.isHeld}
      key={die.id}
      test={() => testing(die.id)}
    />
  )

  const styles = {
    height: "10px",
    margin: "0 0 30px"
  }
  const { width, height } = useWindowSize
  return (
    <main>
      {tenzies && <Confetti
        width={width}
        height={height}
      />}

        {tenzies ? 
      <img src={Voice_gif} alt='' width="30px" style={{'margin-right': 'auto'}}/>
          : ''}

      <h1 className="title">Tenzies</h1>
      <p className="instructions">Roll until all dice are the same. Click each die to freeze it at its current value between rolls.</p>
      <div className="dice-container">
        {diceElements}
      </div>
      <div style={styles}>
        {tenzies && <h2>You won the game🥳</h2>}
      </div>
      <h3>Count: {count}</h3>
      <button
        className='roll-dice'
        onClick={changeNums}
      >
        {tenzies ? "New Game" : "Roll"}
      </button>

        {tenzies ? 
      
      <audio autoPlay preload='true'>
        <source src={aud}/>
        Your browser does not support the audio element.
      </audio>
      : ''}
    </main>
  );
}

export default App;
